html, body {
  font-family: 'Open Sans', sans-serif;
  font-size: 14px; }

a:hover {
  text-decoration: none; }

.sidebar {
  height: 100%;
  position: fixed;
  top: 0;
  left: 0;
  padding: 0;
  margin: 0;
  background: white;
  background: -moz-linear-gradient(top, #ffffff 0%, #ebf1f4 100%);
  background: -webkit-gradient(left top, left bottom, color-stop(0%, #ffffff), color-stop(100%, #ebf1f4));
  background: -webkit-linear-gradient(top, #ffffff 0%, #ebf1f4 100%);
  background: -o-linear-gradient(top, #ffffff 0%, #ebf1f4 100%);
  background: -ms-linear-gradient(top, #ffffff 0%, #ebf1f4 100%);
  background: linear-gradient(to bottom, #ffffff 0%, #ebf1f4 100%);
  filter: progid:DXImageTransform.Microsoft.gradient(startColorstr='#ffffff', endColorstr='#ebf1f4', GradientType=0); }
  @media (max-width: 768px) {
    .sidebar {
      height: auto;
      position: relative; } }

.navbar {
  border: none;
  margin: 0; }
  .navbar .navbar-header {
    width: 100%; }

.logo_block {
  overflow: hidden; }

.logo_wrap {
  margin: 24px 0;
  overflow: hidden; }
  @media (max-width: 768px) {
    .logo_wrap {
      margin: 16px 0 16px; } }

.brand-link {
  display: block;
  position: relative; }
  .brand-link img {
    margin: 0 auto; }
  .brand-link:after, .brand-link:before {
    content: '';
    width: calc(50% - 90px);
    margin: 0 20px;
    height: 1px;
    background: #000000;
    display: block;
    position: absolute;
    top: 50%;
    left: 0;
    transition: all 150ms; }
    @media (max-width: 992px) {
      .brand-link:after, .brand-link:before {
        width: calc(50% - 70px);
        margin: 0 10px; } }
    @media (max-width: 768px) {
      .brand-link:after, .brand-link:before {
        width: calc(50% - 140px);
        margin: 0 70px; } }
  .brand-link:after {
    left: auto;
    right: 0; }

.logo_sign {
  display: block;
  width: 140px;
  text-align: center;
  margin: 0 auto;
  font-size: 10px;
  color: #bdc0c5;
  text-transform: uppercase; }
  @media (max-width: 768px) {
    .logo_sign {
      width: 100%;
      margin: 0 auto 32px; } }

.navbar-toggle {
  position: absolute;
  top: 0;
  right: -8px;
  z-index: 999;
  border-color: #4c596e; }
  .navbar-toggle .icon-bar {
    background-color: #4c596e; }

#main-navbar {
  padding: 0;
  margin: 100px 0 0;
  max-height: 100%;
  position: relative;
  z-index: 20; }
  @media (max-width: 768px) {
    #main-navbar {
      margin: 0 auto 48px; } }
  @media (max-width: 768px) {
    #main-navbar .navbar-nav {
      margin: 0; } }
  #main-navbar .navbar-nav > li {
    width: 100%;
    text-align: center;
    text-transform: uppercase; }
    #main-navbar .navbar-nav > li a {
      color: #bdc0c5;
      font-weight: 300;
      font-size: 22px;
      transition: all 150ms; }
      #main-navbar .navbar-nav > li a:hover, #main-navbar .navbar-nav > li a:focus {
        background: none;
        color: #162022;
        font-weight: 600; }
    #main-navbar .navbar-nav > li.active {
      border-right: 2px solid;
      border-left: 2px solid;
      border-color: #4c596e; }
      #main-navbar .navbar-nav > li.active a {
        color: #162022;
        font-weight: 600; }

.sidebar_footer {
  position: absolute;
  z-index: 10;
  bottom: 0;
  left: 0;
  width: 100%;
  height: auto; }
  @media (max-width: 768px) {
    .sidebar_footer {
      height: 100%; } }

.callback_block {
  display: block;
  width: 100%;
  position: relative;
  text-align: center;
  left: auto;
  bottom: auto; }
  @media (max-width: 768px) {
    .callback_block {
      position: absolute;
      bottom: 4px;
      left: 0; } }

.callback_block_title {
  font-size: 16px;
  position: relative;
  color: #4c596c;
  text-align: center;
  display: block;
  margin-bottom: 30px; }
  @media (max-width: 768px) {
    .callback_block_title {
      display: inline-block;
      vertical-align: middle;
      margin-bottom: 0; } }
  .callback_block_title:after, .callback_block_title:before {
    content: '';
    width: calc(50% - 90px);
    margin: 0 10px;
    height: 1px;
    background: #000000;
    display: block;
    position: absolute;
    top: 50%;
    left: 0;
    transition: all 150ms; }
    @media (max-width: 992px) {
      .callback_block_title:after, .callback_block_title:before {
        width: calc(50% - 90px);
        margin: 0 10px; } }
    @media (max-width: 768px) {
      .callback_block_title:after, .callback_block_title:before {
        display: none; } }
  .callback_block_title:after {
    left: auto;
    right: 0; }

.callback_block_phone {
  font-size: 22px;
  font-weight: 700;
  color: #162022;
  margin-bottom: 10px; }
  @media (max-width: 768px) {
    .callback_block_phone {
      margin-bottom: 0;
      display: inline-block;
      vertical-align: middle; } }

.exit_block {
  width: 100%;
  display: block;
  text-align: center;
  padding: 50px 0; }
  @media (max-width: 768px) {
    .exit_block {
      padding: 0;
      position: absolute;
      top: 8px;
      left: 0;
      width: 100px; } }

.btn-exit {
  width: 80%;
  background-color: #162022;
  color: #fff;
  font-weight: 600;
  font-size: 16px; }
  .btn-exit:hover {
    color: #fff; }

.content {
  padding: 0;
  box-sizing: border-box; }

.content_title_wrapper {
  height: 160px;
  background: #4c596c;
  overflow: hidden; }

.content_title {
  color: #fff;
  text-transform: uppercase; }

.content_container {
  padding: 50px 45px;
  box-sizing: border-box; }

.icon_edit {
  color: #5fc0dc;
  margin: 0 8px;
  transition: color 300ms; }
  .icon_edit:hover {
    color: #288eac; }

.icon_delete {
  color: #d75553;
  margin: 0 8px;
  transition: color 300ms; }
  .icon_delete:hover {
    color: #a72d2b; }

.table_wrapper {
  -webkit-border-radius: 5px;
  -moz-border-radius: 5px;
  border-radius: 5px;
  border: 1px solid #dddddd;
  padding: 30px 15px; }

.table {
  margin: 0; }

